body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
a.button{
  background:#b5e853;
  color:#fff;
  border:none;
  position:relative;
  height:60px;
  font-size:1.6em;
  padding:0 2em;
  cursor:pointer;
  -webkit-transition:800ms ease all;
  -o-transition:800ms ease all;
  transition:800ms ease all;
  outline:none;
}
a.button:hover{
  background:#666;
  color:#b5e853;
}
a.button:before,a.button:after{
  content:'';
  position:absolute;
  top:0;
  right:0;
  height:2px;
  width:0;
  background: #00b715;
  -webkit-transition:400ms ease all;
  -o-transition:400ms ease all;
  transition:400ms ease all;
}
a.button:after{
  right:inherit;
  top:inherit;
  left:0;
  bottom:0;
}
a.button:hover:before,a.button:hover:after{
  width:100%;
  -webkit-transition:800ms ease all;
  -o-transition:800ms ease all;
  transition:800ms ease all;
}


